  width: 30px;
  height: 30px;
  background-color: #fff;
  border-color: rgba(0, 0, 0, 0);
}

.input-field {
  height: 30px;
}

.gallery {
  flex-wrap: wrap;
  display: flex;
}

.photo-card {
  flex-basis: calc(25% - 120px);
  margin-top: 30px;
  margin-left: 30px;
}

.info {
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
